<template>

        <div>
   <div>
           <i class="el-icon-user-solid yuan"></i>
		<el-row v-if="name!=''">
			<el-col :span="2">{{name}}!</el-col>
		<span @click="loginout">注销</span>
		</el-row>
   </div>
   <div>
       <span class="spp"><i class="el-icon-loading"></i>&nbsp;&nbsp;&nbsp;>&nbsp;&nbsp;&nbsp;一级分类&nbsp;&nbsp;&nbsp;>&nbsp;&nbsp;&nbsp;{{titl}}</span>
   </div>
        </div>

</template>

<script>
	export default {
		name: 'tou',
		data() {
			return {
				msg: "",
                name: "",
                
			}
		},   props:{
        titl:{
            type:String,//传值 类型
            default:'系统首页'//默认显示值
            //父向子传值 
        }
    },
		created() {
			this.checkLogin()
		},
		methods: {
			checkLogin() {
				var user = JSON.parse(localStorage.getItem("user"));
				if (user) {
					this.name = user.ick
				}
			},
			loginout() {
				localStorage.removeItem("user")
				this.$message({
					message: '注销成功！',
					type: 'success'
				});
				this.$router.push({
					path: "/"
				})
			}
		}
	}
</script>

<style scoped>
.el-row{

    padding-top:20px; 
    margin-left: 10px
}
.yuan{
    display: inline-block;
 
   color: rgb(0, 121, 254);
 float: left;
 border-radius: 50%;
    margin-left: 890px;
margin-top: 20px;
font-size: 20px
}
.spp{
    display: inline-block;
    font-size: 13px;
    color: #999999;
}
</style>