<template>
  <div class="bei">
    <h1 class="h">登陆</h1>
    <el-input v-model="form.ick" placeholder="请输入内容" class="ip"></el-input>
    <el-input placeholder="请输入密码" v-model="form.ick1" show-password class="ip"></el-input>
    <br />
    <el-button type="success" class="bt" @click="dian">成功按钮</el-button>
  </div>
</template>
<script>
export default {
  name: "Login",
  data() {
    return {
      form: {
        ick: "",
        ick1: ""
      }
    };
  },
  methods: {
    dian() {
      if (this.form.ick == "admin" && this.form.ick1 == "123") {
        var user = JSON.stringify(this.form);
        this.$message({
          message: "恭喜你，登录成功！",
          type: "success"
        });
        localStorage.setItem("user", user);
        this.$router.push({
          path: "/Shou"
        });
      } else {
        this.$message("用户名或密码错误！");
      }
    }
  }
};
</script>
<style scoped>
.h {
  color: darkturquoise;
  margin-left: 50%;
  margin-top: 10%;
}

.bei {
  position: absolute;
  width: 100%;
  height: 100%;

  background-color: #ffccff;
  margin-left: -10px;
  margin-top: -10px;
}
body {
  padding: 0px;
  margin: 0px;
}
.ip {
  width: 20%;
  margin-left: 42.5%;
  margin-top: 20px;
}
.bt {
  margin-top: 20px;
  margin-left: 49%;
  background-color: aqua;
}
</style>